@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,500;0,600;1,200&family=Xanh+Mono:ital@0;1&display=swap);
@font-face {
  font-family: 'director';
  src: local('director'), url(/static/media/director.7240e8c5.woff) format('woff');
}

@font-face {
  font-family: 'ft88';
  src: local('ft88'), url(/static/media/FT88-Regular.5477a5fd.woff) format('woff');
}

@font-face {
  font-family: 'resistance';
  src: local('resistance'),
    url(/static/media/resistance_generale-webfont.48d13596.woff) format('woff');
}

@font-face {
  font-family: 'argent';
  src: local('argent'), url(/static/media/ArgentPixelCF-Regular.3eefbeb1.woff) format('woff');
}

@font-face {
  font-family: 'argent-italics';
  src: local('argent-italics'),
    url(/static/media/ArgentPixelCF-Italic.9ca31ce5.woff) format('woff');
}

body .App {
  margin: 0;
  font-family: 'director';
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: medium;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 100;
  }
}

@keyframes fade-out {
  from {
    opacity: 100;
  }
  to {
    opacity: 0;
  }
}

html {
  background-color: black;
  transition: 0.5s ease;
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease;
  transition-delay: 0s;
  scroll-behavior: inherit;
}

button {
  animation: fade-in 5s;
  font-family: 'director';
  cursor: pointer;
}

.sticky-note {
  animation: fade-in 1s fade-out 1s;
  position: absolute;
  left: 0;
  right: 0;
  top: -150px;
  bottom: 0;
  margin-top: auto;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: auto;
  transition: transform 0.5s ease-in-out;
  width: 350px;
  height: 350px;
  z-index: 15;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.sticky-note img {
  position: absolute;
  width: 350px;
  height: 350px;
  /* box-shadow: 0 0 25px rgba(0, 0, 0, 0.2); */
}

.sticky-note:hover {
  transform: rotate(2deg);
}

.sticky-title {
  width: 200px;
}

.game-title {
  font-weight: 500;
  font-size: larger;
  margin-bottom: 20px;
  font-family: 'resistance';
  /* text-transform: ; */
}

.game-subtitle {
  /* font-size: ; */

  font-family: 'argent-italics';
  /* text-transform: uppercase; */
}

.main-screen {
  animation: fade-in 5s;
  /* position: absolute; */
  position: relative;
  width: 900px;
  height: 650px;
  background-color: black;
  /* left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin-top: auto;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: auto; */
  overflow: hidden;
  cursor: url(data:image/png;base64,), auto;
}

#screen-tex {
  mix-blend-mode: hard-light;
  top: 0;
  left: 0;
  position: absolute;
  z-index: 10;
  width: 100%;
  height: 120%;

  opacity: 0.4;
  pointer-events: none;
}

#halftone-tex {
  mix-blend-mode: color-burn;
  top: 0;
  left: 0;
  position: absolute;
  z-index: 10;
  width: 100%;
  height: 120%;

  opacity: 0.2;
  pointer-events: none;
}

.flavour {
  animation: fade-in 5s;
  margin-top: 20px;
  height: 100px;
  width: 850px;
  color: white;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Login {
  animation: fade-in 5s;
  background-color: #1667ae;
  position: relative;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.wipe-button {
  position: absolute;
  bottom: 50px;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  pointer-events: all;
}

img.login-icon {
  border-radius: 100%;
  /* margin-bottom: 10px; */
  width: 75px;
  height: 75px;
}

.login-username {
  color: white;
  font-weight: 600;
  margin: 10px 0px 20px 0px;
}

.login-label {
  font-size: small;
  margin-bottom: 5px;
  color: white;
}

.login-button {
  margin: 5px;
  margin-top: 20px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.login-fields {
  margin: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* align-items: flex-start; */
}

.Login .window {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  z-index: 2;
}

.App .title-bar,
.App .window,
.App button,
.App input,
.App label,
.App option,
.App select,
.App table,
.App textarea,
.App ul.tree-view {
  animation: none;
  -webkit-font-smoothing: none;
  font-family: 'director';
}

button.wipe-button {
  animation: fade-in 5s;
}

.window-photo {
  width: 326px;
}

.window-folder-photo {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  top: -200px;
  z-index: 2;
}

.Desktop {
  animation: fade-in 5s;
  background-image: url(/static/media/desktop.1a59c316.png);
  position: relative;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

button.logout-button {
  animation: fade-in 6s;
  font-weight: 800;
  position: absolute;
  bottom: 0;
  margin: 3px;
  height: 30px;
  right: 120px;
  box-shadow: -6px -7px 7px -1px rgba(0, 0, 0, 0.75) inset;
  box-shadow: 6px 5px 7px -1px rgba(255, 255, 255, 0.75) inset;
  -webkit-box-shadow: 6px 5px 7px -1px rgba(255, 255, 255, 0.75) inset;
  -moz-box-shadow: 6px 5px 7px -1px rgba(255, 255, 255, 0.75) inset;
}

.top-window {
  position: relative;
  z-index: 8;
}

.text-file {
  z-index: 8;
  /* position: absolute;

  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0; */
  height: 467px;
  width: 450px;
  background-image: url(data:image/png;base64,);
}

.text-file-folder {
  z-index: 8;
  position: absolute;
  top: -250px;
  bottom: 0;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  height: 467px;
  width: 450px;
  background-image: url(data:image/png;base64,);
}

.img-file-folder {
  z-index: 8;
  position: absolute;
  top: -250px;
  bottom: 0;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  background-image: url(data:image/png;base64,);
}

.text-file-content {
  font-family: 'Xanh Mono', monospace;
  font-weight: 400;
  font-style: normal;
  position: absolute;
  margin: 8px;
  padding: 10px;
  top: 20px;
  left: 0px;
  width: 415px;
  height: 390px;
  background-color: white;
  font-size: small;
  overflow: scroll;
  text-align: left;
}

.Icon {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: antiquewhite;
  font-size: small;
  text-align: center;
  width: 70px;
}

.Icon img {
  width: 35px;
  margin: 10px;
}

.icon-label {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow-wrap: break-word;
  max-width: 75px;
  display: inline-block;
}

.finder-window {
  width: 450px;
  height: 360px;
  background-image: url(/static/media/finder.2afac1a6.png);
  position: relative;
  z-index: 2;
}

.finder-name-topbar {
  position: absolute;
  top: 8px;
  left: 32px;
  color: antiquewhite;
  font-size: small;
}

.finder-name-address {
  position: absolute;
  top: 80px;
  left: 89px;
  color: black;
  font-size: small;
}

