.close-finder-button {
  position: absolute;
  top: 7px;
  right: 7px;
  width: 17px;
  height: 16px;

  cursor: pointer;
}

.close-finder-button:hover {
  background-color: rgba(0, 0, 0, 0.109);
  border: solid 1px black;
}

.email-select-window {
  width: 300px;
  height: 400px;
}

table {
  width: 100%;
}

.inbox-body {
  display: flex;
}

.inbox-body-right {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.email-metadata {
  text-align: left;
  max-height: 50px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  padding: 5px 10px 5px 10px;

  width: 360px;
}
.inbox-body-text {
  padding: 10px;
  text-align: left;
}

